import React, { Component } from "react";
import { NavLink } from "react-router-dom";


export default class Navbar extends Component {
  render() {
    return (
      <div className="navbar">
        <NavLink exact to="/" >
          Inicio
        </NavLink>
        <NavLink exact to="/odontologists" >
          Odontólogos
        </NavLink>
        <NavLink exact to="/patients" >
          Pacientes
        </NavLink>
      </div>
    );
  }
}