import React, { Component } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Navbar from "./components/Navbar";
import Home from "./components/Home";
import Odontologists from "./components/Odontologists";
import Patients from "./components/Patients";
import PatientsRegister from "./components/PatientsRegister";
import OdontologistsRegister from "./components/OdontologistsRegister";

export default class App extends Component {
  render() {
    return (
    <BrowserRouter>
      <Navbar />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/odontologists" component={Odontologists} />
        <Route exact path="/odontologists/register" component={OdontologistsRegister} />
        <Route exact path="/patients" component={Patients} />
        <Route exact path="/patients/register" component={PatientsRegister} />
      </Switch>
      
    </BrowserRouter>

  );
}}
