import React, { Component } from "react";
import axios from 'axios';


export default class RegistroOdontologos extends Component {

    constructor(props){
        super(props);
        this.state = {
                name: "",
                lastName: "",
                licenseNumber: ""
            }
        }
    

    handleChange = (event) => {
        this.setState({[`${event.target.name}`]:event.target.value});
    };

    handleSubmit = (e) => {
        e.preventDefault();
        const article = { title: 'Register Odontologist' };
        axios.post(
            "localhost:8080/odontologists/register",
            JSON.stringify({
                name: this.state.name,
                lastName: this.state.lastName,
                licenseNumber: this.state.licenseNumber,
            }),article,
            {
              headers: {
                "Content-type": "application/json;charset=utf-8",
              }
            }
          )
    };

    render() {
            return (
                <div id="div_odontologists_updating">
                    <form id="update_odontologists_form" method="post">
                
                            <label htmlFor="name">Nombre:
                            <input type="text" placeholder="Nombre" name="name" onChange={this.handleChange}/></label>

                            <label htmlFor="lastName">Apellido:
                            <input type="text" placeholder="Apellido" name="lastName" onChange={this.handleChange}/></label>

                            <label htmlFor="licenseNumber">Matrícula:
                            <input type="text"  placeholder="Matrícula" name="licenseNumber" onChange={this.handleChange}/></label>
                    
                    
                        <button type="submit" onSubmit={this.handleSubmit}>Submit</button>
                    </form>
                    <div id="response"></div>
                </div>
            )
}}

