import * as React from 'react';

export default function Table({title}) {
  return (
    <div id={title + "_table"}>
        <h2>{title}</h2>
        <div class="row col-md-6 table-responsive">
            <table id="odontologoTable" class="table table-striped">
                <thead>
                <tr>
                    <th>Id</th>
                    <th>Nombre</th>
                    <th>Apellido</th>
                    <th>{title === "Pacientes" ? "Dni" : "Matrícula"}</th>
                    {/*<th className={title === "Pacientes" ? "domicilioTrue" : "domicilioFalse"}>Domicilio</th>*/}
                    <th style={title === "Pacientes" ? {display: ''} : {display: 'none'}}>Domicilio</th>
                </tr>
                </thead>
                <tbody>
                </tbody>
            </table>
        </div>
    </div>
  );
}