export default function RegistroOdontologos() {
    return (
        <div id="div_odontologo_updating">
            <form id="update_odontologo_form">
        
                    <label >Nombre:
                    <input type="text" placeholder="nombre" id="name"/></label>


                    <label >Apellido:
                    <input type="text" placeholder="apellido" id="lastName"/></label>

                    <label>DNI:
                    <input type="text"  placeholder="dni" id="dni"/></label>
               
            
                <button type="submit" >Submit</button>
            </form>
            <div id="response">
            </div>
        </div>
    )
}